import { Component, h, Host, Prop, Element } from '@stencil/core';

/**
 * Informs user about important changes or conditions in the interface. Use this
 * component if you need to capture users attention in a prominent way.
 */
@Component({
  tag: 'cat-alert',
  styleUrl: 'cat-alert.scss',
  shadow: true
})
export class CatAlert {
  private readonly mapIcon: Map<string, string> = new Map([
    ['primary', 'star-circle-filled'],
    ['secondary', 'clock-filled'],
    ['success', 'check-circle-filled'],
    ['warning', 'danger-filled'],
    ['danger', 'cross-circle-filled']
  ]);
  private readonly mapRole: Map<string, string> = new Map([
    ['primary', 'status'],
    ['secondary', 'status'],
    ['success', 'status'],
    ['warning', 'alert'],
    ['danger', 'alert']
  ]);

  @Element() hostElement!: HTMLElement;

  /**
   * The color palette of the alert.
   */
  @Prop() color: 'primary' | 'secondary' | 'danger' | 'success' | 'warning' = 'primary';

  /**
   * The name of an icon to be displayed in the alert.
   */
  @Prop() icon?: string;

  /**
   * Whether the icon of the alert is deactivated.
   */
  @Prop() noIcon = false;

  render() {
    return (
      <Host tabindex={this.tabIndex} role={this.role}>
        {!this.noIcon && <cat-icon size="l" icon={this.icon ? this.icon : this.mapIcon.get(this.color)}></cat-icon>}
        <div class="content">
          <slot></slot>
        </div>
      </Host>
    );
  }

  private get tabIndex() {
    return this.hostElement.getAttribute('tabindex') || '0';
  }

  private get role() {
    return this.hostElement.getAttribute('role') || this.mapRole.get(this.color) || null;
  }
}
